import useSwapStore from '@/store/swap/swap'
import { SwapRouterData } from '@/types/swap'
import { Heading, Modal, ModalBody, ModalCloseButton, ModalContent, ModalHeader, ModalOverlay } from '@chakra-ui/react'
import SwapConfirmBlock from '../swap/SwapConfirmBlock'

type SwapConfirmModelProps = {
  data: SwapRouterData
  isOpen: boolean
  onClose: () => void
  onSubmitClick: (data: SwapRouterData) => void
}

export default function SwapConfirmModel(props: SwapConfirmModelProps) {
  const { isOpen, onClose, data, onSubmitClick } = props
  const { fromCoin, toCoin, routerData } = useSwapStore()

  return (
    <Modal autoFocus={false} returnFocusOnClose={false} trapFocus={false} isOpen={isOpen} onClose={onClose} isCentered>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>
          <Heading fontWeight="500" fontSize="16px">
            Swap
          </Heading>
        </ModalHeader>
        <ModalCloseButton />
        <ModalBody textAlign="center" p="0px">
          <SwapConfirmBlock
            onSubmitClick={onSubmitClick}
            onClose={onClose}
            data={data}
            lastRouterData={routerData}
            fromCoin={fromCoin!}
            toCoin={toCoin!}
            isWidget={false}
          />
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}
