import { aggregatorStatusPath } from '@/apis/path'
import { useFetch } from '@cetus/hooks'
import useWebConfigStore from '@cetus/stores/src/useWebConfigStore'

export default function useGetRouterConfig() {
  const { fetchByApi } = useFetch()
  const { setProviders } = useWebConfigStore()

  /**
   * Ëé∑ÂèñrouterÈÖçÁΩÆ‰ø°ÊÅØ
   * TODO ÊØèÊ¨°ÊâìÂºÄË∞ÉÁî®‰∏ÄÊ¨°Ôºü
   */
  const fetchRouterConfig = async () => {
    try {
      const res = await fetchByApi(aggregatorStatusPath, 'GET')
      const { providers } = res
      if (providers && providers.length > 0) {
        setProviders(providers)
      }
      console.log('üöÄ ~ file: useGetRouterConfig.ts:13 ~ fetchRouterConfig ~ res:', res)
    } catch (error) {
      console.log('üöÄ ~ file: useGetRouterConfig.ts:18 ~ fetchRouterConfig ~ error:', error)
    }
  }

  return {
    fetchRouterConfig
  }
}
